import React from 'react'
import { ColorOrganizerHeader } from './header/color-organizer-header.js'
import { Color } from './color/color.js'

import './colorOrganizer.scss'

export default class ColorOrganizer extends React.Component {

  constructor (props) {
    super(props)
    this.state = {
      colors: [
        { 
          color: { backgroundColor: '#004080' },
          title: "favorite blue",
          starSelected: 3,
          id: "2345"
        }
      ]
    }
    this.onRate = this.onRate.bind(this)
  }

  onRate (num) {
    const msg = `you rated! num: ${num}`
    window.alert(msg)
  }

  render () {
    const color = { backgroundColor: "#333" }
    const title = "sample color"
    const starLimit = 5
    const starSelected = 3
    
    return (
      <div className="color-organizer-container">
        <ColorOrganizerHeader />
        <div className="color-organizer-body u-flex--item-table">
          <Color color={color}
            title={title}
            starLimit={starLimit}
            starSelected={starSelected}
            onRate={this.onRate} />
        </div>
      </div>
    )
  }
}



